import React, { Fragment } from "react"
import { Helmet } from "react-helmet"
import { useKeenSlider } from "keen-slider/react"

import Head from "../components/head/index"
import Place from "../components/place/index"
import Sayhay from "../components/sayhay"
import Footer from "../components/footer"
import AudioPlayer from "../components/audioPlayer"

import metaImg from "../images/bg.jpeg"
import loveImg from "../images/love.png"

import "keen-slider/keen-slider.min.css"
import "../styles/normalize.css"

const WheelControls = slider => {
  let touchTimeout
  let position
  let wheelActive

  function dispatch(e, name) {
    position.x -= e.deltaX
    position.y -= e.deltaY
    slider.container.dispatchEvent(
      new CustomEvent(name, {
        detail: {
          x: position.x,
          y: position.y,
        },
      })
    )
  }

  function wheelStart(e) {
    position = {
      x: e.pageX,
      y: e.pageY,
    }
    dispatch(e, "ksDragStart")
  }

  function wheel(e) {
    dispatch(e, "ksDrag")
  }

  function wheelEnd(e) {
    dispatch(e, "ksDragEnd")
  }

  function eventWheel(e) {
    e.preventDefault()
    if (!wheelActive) {
      wheelStart(e)
      wheelActive = true
    }
    wheel(e)
    clearTimeout(touchTimeout)
    touchTimeout = setTimeout(() => {
      wheelActive = false
      wheelEnd(e)
    }, 50)
  }

  slider.on("created", () => {
    slider.container.addEventListener("wheel", eventWheel, {
      passive: false,
    })
  })
}

export function Landing({ location }) {
  const metaTitle = "Pernikahan Dea dan Ziqri"
  const [sliderRef] = useKeenSlider(
    {
      loop: false,
      rubberband: false,
      vertical: true,
    },
    [WheelControls]
  )

  return (
    <Fragment>
      <Helmet
        title={metaTitle}
        meta={[
          {
            property: `og:title`,
            content: metaTitle,
          },
          {
            property: `og:image`,
            content: metaImg,
          },
        ]}
      />

      <link rel="icon" type="image/png" href={loveImg} sizes="16x16" />

      <div ref={sliderRef} className="keen-slider" style={{ height: "100vh" }}>
        <div className="keen-slider__slide">
          <Head />
        </div>
        <div className="keen-slider__slide">
          <Sayhay location={location} />
        </div>
        <div className="keen-slider__slide">
          <Place />
        </div>

        <div className="keen-slider__slide">
          <Footer />
        </div>
      </div>

      <AudioPlayer />
    </Fragment>
  )
}

export default Landing
