import React, { useEffect, useState } from 'react';
import moment from 'moment-timezone';
import styles from './style.module.css';

function Countdown() {
  const [time, setTime] = useState({
    months: 0,
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  })
  const { months, days, hours, minutes, seconds } = time;
  let eventInterval;
  const gCalendar = 'https://calendar.google.com/calendar/u/0/r/eventedit?text=The+Wedding+of+Dea+%26+Ziqri&location=Ponyo,%20Jl.%20Kemakmuran%20No.21,%20RT.006/RW.005,%20Marga%20Jaya,%20Kec.%20Bekasi%20Sel.,%20Kota%20Bks,%20Jawa%20Barat%2017141,%20Indonesia&dates=20240512T093000/20240512T123000';

  /**
   * set interval
   */
  useEffect(() => {
    eventInterval = setInterval(startEvent, 1000);    

    return () => {
      clearInterval(eventInterval);
    }
  }, [])

  return (
    <div className={styles['wrapper']}>
      <div className={styles['goTo']}>Menuju Hari Bahagia</div>
      <a className={styles['btn']} href={gCalendar} target="_blank">Simpan Ke Kalender</a>
      <div className={styles['timerWrapper']}>
        {months > 0 && (
          <div className={styles['boxtime']}>
            <div className={styles['box']}>
              <div>{months}</div>
              <label>Bulan</label>
            </div>
          </div>
        )}

        <div className={styles['boxtime']}>
          <div className={styles['box']}>
            <div>{days}</div>
            <label>Hari</label>
          </div>
        </div>

        <div className={styles['boxtime']}>
          <div className={styles['box']}>
            <div>{hours}</div>
            <label>Jam</label>
          </div>
        </div>

        <div className={styles['boxtime']}>
          <div className={styles['box']}>
            <div>{minutes}</div>
            <label>Menit</label>
          </div>
        </div>

        <div className={styles['boxtime']}>
          <div className={styles['box']}>
            <div>{seconds}</div>
            <label>Detik</label>
          </div>
        </div>
      </div>
    </div>
  )

  /**
   * fungsi untuk memulai event
   */
  function startEvent() {
    const currentTime = moment.tz();
    const eventTime = moment.tz("2024-05-12 09:00", "Asia/Bangkok");
    const duration = moment.duration(eventTime.diff(currentTime));

    if (duration.asSeconds() <= 0) { 
      clearInterval(eventInterval);
      setTime({
        months: 0,
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0
      });
    } else {
      setTime({
        months: duration.months(),
        days: duration.days(),
        hours: duration.hours(),
        minutes: duration.minutes(),
        seconds: duration.seconds()
      });
    }
  }
};

export default Countdown;